<template>
  <div class="my-checkbox-group">
    <slot></slot>
  </div>
</template>

<script>
export default {
  name: 'myCheckboxGroup',
  data() {
    return {
      options: [], // éé¡¹
    }
  },
  props: {
    // éä¸­å¼
    value: {
      type: Array,
      default: () => [],
    },
    disabled: {
      type: Boolean,
      default: false,
    },
    // éå¶æå¤éæ©æ°éï¼-1ä¸ºä¸éå¶
    max: {
      type: Number,
      default: -1,
    },
    // éå¶æå°éæ©æ°éï¼-1ä¸ºä¸éå¶
    min: {
      type: Number,
      default: -1,
    },
    // æ¯å¦å¯ç¨æé®æ ·å¼
    button: {
      type: Boolean,
      default: false,
    },
  },
  watch: {
    value: {
      handler(newVal) {
        this.initValue()
      },
      immediate: true,
    },
    // ç¦ç¨ç¶æ
    disabled: {
      handler(newVal) {
        this.syncOptionsDisable(newVal)
      },
      immediate: true,
    },
    // æ¯å¦ä½¿ç¨æé®æ ·å¼
    button: {
      handler(newVal) {
        this.syncOptionsButtonStyle(newVal)
      },
      immediate: true,
    },
  },
  created() {
    // çå¬on-checkbox-addäºä»¶ï¼å°checkboxå®ä¾å­å°optionsä¸
    this.$on('on-checkbox-add', (checkbox) => {
      this.options.push(checkbox)
      this.initValue()
      this.syncOptionsDisable(this.disabled)
      this.syncOptionsButtonStyle(this.button)
    })
    // çå¬on-checkbox-removeäºä»¶ï¼å°checkboxå®ä¾ä»optionsä¸­ç§»é¤
    this.$on('on-checkbox-remove', (checkbox) => {
      this.options.splice(this.options.indexOf(checkbox), 1)
      this.syncValue()
    })
    // çå¬checkboxçéä¸­äºä»¶ï¼åvalueå¼åæ­¥
    this.$on('on-checkbox-select', () => {
      this.syncValue()
    })
  },
  methods: {
    initValue() {
      this.options.forEach((cell) => {
        cell.checked = this.value.find((d) => d === cell.label)
      })
      this.$nextTick(() => {
        this.optionsLimit()
      })
    },
    /**
     * @description valueå¼åæ­¥
     */
    syncValue() {
      let currentValue = this.options.reduce((currentArr, cell) => {
        cell.checked && currentArr.push(cell.label)
        return currentArr
      }, [])
      this.$emit('input', currentValue)
      this.$emit("change", currentValue);
      this.optionsLimit()
    },
    /**
     * @description è®¾ç½®å­éé¡¹çmyDisabledå±æ§
     * @param {Boolean} disabled æ¯å¦ç¦ç¨
     */
    syncOptionsDisable(disabled) {
      this.options.forEach((d) => {
        d.myDisabled = disabled
      })
    },
    /**
     * @description æ°ééå¶æä½
     */
    optionsLimit() {
      this.$nextTick(() => {
        this.options.forEach((cell) => {
          cell.limitDisabled = false
        })

        const length = this.value.length
        // æå¤§å¼éå¶
        if (length >= this.max && this.max > -1) {
          this.options.forEach((cell) => {
            if (!cell.checked) {
              cell.limitDisabled = true
            }
          })
        }
        // æå°å¼éå¶
        if (length <= this.min && this.min > -1) {
          this.options.forEach((cell) => {
            if (cell.checked) {
              cell.limitDisabled = true
            }
          })
        }
      })
    },
    /**
     * @description è®¾ç½®å­éé¡¹çbuttonå±æ§ï¼ç¨ä»¥æ§å¶æé®æ ·å¼
     * @param {Boolean} value æ¯å¦è®¾ç½®
     */
    syncOptionsButtonStyle(value) {
      this.options.forEach((d) => {
        d.button = value
      })
    },
  },
}
</script>
